// fetch weather data from api by location, return weather data for location in an object
const API_KEY = '2b7df015d5d73cef01a306c61a9eb986';

async function fetchDataByLocation() {
	const UNIT_SYSTEM = 'metric';
    const LOCATION = 'London, UK';

	const response = await fetch(
		`https://api.openweathermap.org/data/2.5/weather?q=${LOCATION}&units=${UNIT_SYSTEM}&appid=${API_KEY}`
	);
	const data = await response.json();
	return data;
}

export { fetchDataByLocation };
